body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
  background-color: #f8f8fa;
  padding: 15px;
  display: block;
  margin: 5px 0;
  overflow: auto;
}

.navbar {
  height: 22px;
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  background-color: white;
  box-shadow: 0 2px 2px -3px rgb(115, 115, 116);
  padding: 20px 10px;
}

.nav-images {
  display: flex;
  float: right;
  position: absolute;
  right: 20px;
  top: 22px;
}

.nav-image {
  margin-left: 20px;
}

.nav-image .npm svg {
  width: 40px;
  height: 15px;
}

.nav-image .github svg {
  width: 20px;
  height: 20px;
}

.sidebar {
  width: 300px;
  position: fixed;
  left: 0;
  bottom: 0;
  top: 60px;
  list-style: none;
  margin: 0;
  padding: 0;
  padding-top: 10px;
  background-color: white;
  z-index: 2;
  box-shadow: 2px 2px 2px -2px #ccc;
}

.sidebar li {
  padding: 5px 30px;
  font-size: 14px;
}

.sidebar li a {
  text-decoration: none;
  color: inherit;
}

.sidebar-sub-menu {
  list-style: none;
  margin: 0;
  padding: 0;
}

.main {
  position: fixed;
  left: 300px;
  top: 60px;
  right: 0;
  bottom: 0;
  padding: 10px;
  overflow: auto;
}

.title {
  font-weight: bold;
}

.title::after {
  content: ":";
}

.example {
  padding: 15px 0;
  border-bottom: 1px solid #ccc;
}

table {
  border: 1px solid #ccc;
}

table th,
table td {
  text-align: center;
  border: 1px solid #ccc;
  padding: 2px 10px;
}

.screenshot {
  display: flex;
  justify-content: center;
}

.screenshot img {
  width: 70%;
}

.open-sidebar {
  cursor: pointer;
  visibility: hidden;
}

.navbar .title {
  margin-left: 5px;
  display: inline-block;
  font-weight: unset;
}

@media (max-width: 650px) {
  .open-sidebar {
    visibility: visible;
    padding: 7px 15px;
    padding-top: 7px;
    font-size: 18px;
  }

  .navbar .title {
    margin-left: 15px;
  }

  .sidebar {
    left: -305px;
    transition: 0.4s;
    box-shadow: 3px 2px 6px -2px #ccc;
  }

  .sidebar.active {
    left: 0;
    transition: 0.4s;
  }

  .main {
    left: 0px;
  }
}

@media (max-width: 400px) {
  .navbar .title {
    display: none;
  }
}
